r\n\r\n/* ---------------------------------- */\r\n/* Contact Page\r\n------------------------------------- */\r\n#contact textarea {\r\n\tmin-height: 200px;\r\n\tmargin: 15px 0 25px 0;\r\n}\r\n\r\n#contact input {\r\n\tmargin-bottom: 25px;\r\n}\r\n\r\n/* .loader { margin-left: 15px; } */\r\n.submit.disabled:hover,\r\n.submit.disabled { background-color: #e8e8e8; color: #333; }\r\n\r\n#contact input.button.submit {\r\n\tmargin-bottom: 10px;\r\n\tline-height: 18px;\r\n\theight: 49px;\r\n\ttransition: all 0.25s !important; \r\n}\r\n\r\n\r\n#contact input[type=\"submit\"].submit:hover {\r\n    opacity: 0.92;\r\n}\r\n\r\n.contact-sent {\r\n\tbackground-color: #EBF6E0;\r\n\tcolor: #5f9025;\r\n\tpadding: 20px 26px;\r\n\tmargin-bottom: 30px;\r\n\tborder-radius: 3px;\r\n}\r\n\r\n\r\n/* Office Address Box */\r\n.office-address {\r\n\ttext-align: center;\r\n\tposition: relative;\r\n\tcolor: #fff;\r\n\tdisplay: table-cell;\r\n\tvertical-align: middle;\r\n\theight: 100%;\r\n}\r\n\r\n.office-address ul {\r\n\tlist-style: none;\r\n\tfont-size: 18px;\r\n\tpadding: 0;\r\n\tline-height: 30px;\r\n}\r\n\r\n.office-address h3 {\r\n\tfont-size: 28px;\r\n\tcolor: #fff;\r\n\tmargin-top: 0;\r\n}\r\n\r\n.office-address h3:after {\r\n\tcontent: \"\";\r\n\theight: 2px;\r\n\twidth: 50px;\r\n\tposition: relative;\r\n\tdisplay: block;\r\n\tbackground-color: #66676b;\r\n\tmargin: 20px auto;\r\n\tborder-radius: 2px;\r\n}\r\n\r\n.address-container {\r\n\tbackground-color: #2b2c30;\r\n\tbackground-size: 100%;\r\n\tbackground-position: 50% 50%;\r\n\tbackground-repeat: no-repeat;\r\n\tbackground-size: cover;\t\r\n\tdisplay: table;\r\n\twidth: 100%;\r\n\tposition: relative;\r\n\theight: 100%\r\n}\r\n\r\n.address-container:before {\r\n\tcontent: \"\";\r\n\theight: 100%;\r\n\twidth: 100%;\r\n\tposition: absolute;\r\n\ttop:0;\r\n\tleft:0;\r\n\tdisplay: block;\r\n\tbackground-color: #333;\r\n\topacity: 0.7;\r\n}\r\n\r\n\r\n/* Google Maps */\r\n.contact-map { display: -webkit-box; display: -ms-flexbox; display: flex; }\r\n.contact-map #singleListingMap-container { -webkit-box-flex: 1; -ms-flex: 1; flex: 1; position: relative; padding-top: 0; }\r\n.contact-map .address-box-container { -webkit-box-flex: 0; -ms-flex: 0 auto; flex: 0 auto; width: 440px; height: 450px; }\r\n.contact-map #singleListingMap-container #singleListingMap { height: 100%; width: 100%; }\r\n\r\n@media (min-width: 1680px) { .contact-map .address-box-container { height: 480px; } }\r\n@media (max-width: 1440px) { .contact-map .address-box-container { height: 420px; } }\r\n\r\n\r\n/* Contact Details */\r\n.sidebar-textbox {\r\n\tdisplay: inline-block;\r\n\twidth: 100%;\r\n\tpadding-right: 40px;\r\n}\r\n\r\n.sidebar-textbox.color {\r\n\tbackground-color: #666;\r\n\tcolor: #fff;\r\n}\r\n\r\n.sidebar-textbox.color h4 {color: #fff;}\r\n.sidebar-textbox h4 {\r\n\tfont-size: 16px;\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n\tmargin-bottom: 16px;\r\n}\r\n\r\n.sidebar-textbox span { color: #666; display: inline-block; }\r\n.sidebar-textbox.color span { color: #fff; }\r\n\r\n.sidebar-textbox ul.contact-details {\r\n\tlist-style: none;\r\n\tpadding: 5px 0 0 0;\r\n}\r\n\r\n.sidebar-textbox ul.contact-details strong {\r\n\tfont-weight: 500;\r\n}\r\n\r\n.sidebar-textbox ul.contact-details li {\r\n\ttext-align: left;\r\n\tpadding-left: 60px;\r\n\tposition: relative;\r\n\twidth: 100%;\r\n\tdisplay: inline-block;\r\n\tmargin: 10px 0;\r\n\tline-height: 24px;\r\n}\r\n\r\n.sidebar-textbox ul.contact-details li a { color: #66676b; }\r\n\r\n.sidebar-textbox ul.contact-details li strong {\r\n\tdisplay: block;\r\n\tcolor: #333;\r\n}\r\n\r\n.sidebar-textbox ul.contact-details li i {\r\n\theight: 100%;\r\n\tposition: absolute;\r\n\tleft: 0;\r\n\tfont-size: 32px;\r\n\tcolor: #333;\r\n\ttop: 10px\r\n}\r\n\r\n\r\n\r\n/* ---------------------------------- */\r\n/* Blog Styles\r\n------------------------------------- */\r\n.blog-post {\r\n\tbox-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);\r\n\tbackground-color: #fff;\r\n\tdisplay: inline-block;\r\n\twidth: 100%;\r\n\tborder-radius: 3px;\r\n\tmargin: 15px 0;\r\n}\r\n\r\nsection.fullwidth .blog-post { margin: 5px 0 0 0; }\r\n\r\n.post-img {\r\n\tposition: relative;\r\n\theight: 100%;\r\n\twidth: 100%;\r\n\tdisplay: block;\r\n}\r\n\r\n.post-img img {\r\n\twidth: 100%;\r\n\tborder-radius: 3px 3px 0 0;\r\n}\r\n\r\n.post-img:after {\r\n\tposition: absolute;\r\n\theight: 100%;\r\n\twidth: 100%;\r\n\tdisplay: block;\r\n\ttop: 0;\r\n\tleft: 0;\r\n\tcontent: \"\";\r\n\tz-index: 9;\r\n\topacity: 0.1;\r\n\tbackground: #333;\r\n\ttransition: opacity 0.4s;\r\n\tborder-radius: 3px 3px 0 0;\r\n}\r\n\r\n.post-img:hover:after {\r\n\topacity: 0.5;\r\n}\r\n\r\n\r\n/* Post Hover Icon */\r\n.post-img:before { \r\n\topacity: 0;\r\n\tvisibility: hidden;\r\n\tposition: absolute;\r\n\tdisplay: block;\r\n\ttop: 50%;\r\n\tright: 0;\r\n\tleft: 0;\r\n\tmargin: 0 auto;\r\n\ttext-align: center;\r\n\tcontent: \"\";\r\n\tz-index: 119;\r\n\ttransition: 0.3s;\r\n\t-webkit-transform: translateY(-80%);\r\n\t        transform: translateY(-80%);\r\n\twidth: 50px;\r\n\theight: 50px;\r\n\ttext-align: center;\r\n\tborder-radius: 50%;\r\n\tbackground-color: #66676b;\r\n\tbackground-image: url(" + require("../images/plus-icon.svg") + ");\r\n\tbackground-repeat: no-repeat;\r\n\tbackground-position: 50%\r\n}\r\n\r\n.post-img:hover:before {\r\n\topacity: 1;\r\n\tvisibility: visible;\r\n\t-webkit-transform: translateY(-50%);\r\n\t        transform: translateY(-50%);\r\n}\r\n\r\n\r\n/* Content Styling */\r\n.post-content { padding: 38px; }\r\n\r\n.post-content p { margin: 18px 0 0 ; }\r\n\r\n.post-content h3 {\r\n\tmargin: 0;\r\n\tline-height: 34px;\r\n\tfont-size: 22px;\r\n}\r\n\r\n.post-content h3 a { color: #333; }\r\n.post-content h3 a:hover {color: #66676b; }\r\n\r\n.post-content .meta-tags span {\r\n\tmargin: 0 0 15px 0;\r\n\tdisplay: inline-block;\r\n\tcolor: #888;\r\n}\r\n\r\n.post-content a.read-more {\r\n\tcolor: #66676b;\r\n\tmargin: 23px 0 2px 0;\r\n\tdisplay: inline-block;\r\n}\r\n\r\na.read-more i {\r\n\tfont-size: 20px;\r\n\tposition: relative;\r\n\tpadding-left: 4px;\r\n\ttop: 1px;\r\n\ttransition: 0.2s;\r\n}\r\n\r\n a.read-more:hover i {\r\n\tpadding-left: 7px;\r\n}\r\n\r\n\r\n/* Post Meta */\r\n.post-meta {\r\n\tlist-style: none;\r\n\tpadding: 0;\r\n\tmargin: 5px 0 0 0;\r\n}\r\n\r\n.post-meta li {\r\n\tdisplay: inline-block;\r\n\tcolor: #888;\r\n}\r\n\r\n.post-meta li:after {\r\n\tcontent: \"\";\r\n\twidth: 1px;\r\n\theight: 12px;\r\n\tbackground: #d0d0d0;\r\n\tdisplay: inline-block;\r\n\tposition: relative;\r\n\ttop: 0px;\r\n\tmargin: 0 10px;\r\n\r\n}\r\n\r\n.post-meta li:last-child:after { display: none; }\r\n\r\n.post-meta li a { color: #888; }\r\n\r\n.post-meta li a:hover {\r\n\tcolor: #66676b;\r\n}\r\n\r\n\r\n/* Blog Page Common Style */\r\n.blog-page .blog-post {\r\n\tmargin: 0 0 45px 0;\r\n}\r\n\r\n.blog-page h4.headline { font-size: 22px !important; font-weight: 400; }\r\n\r\n/* Search Widget */\r\n.search-blog-input { position: relative; }\r\n\r\n.widget .search-blog-input .input:before {\r\n\tcontent: \"\\E090\";\r\n\tfont-size: 17px;\r\n\tcolor: #a0a0a0;\r\n\tposition: absolute;\r\n\tdisplay: inline-block;\r\n\tfont-family: \"simple-line-icons\";\r\n\tright: 18px;\r\n\ttop: 13px;\r\n\tmargin-bottom: 0;\r\n\tbackground-color: #fff;\r\n\tpadding-left: 7px;\r\n}\r\n\r\n#contact textarea,\r\n#contact input,\r\n.widget .search-blog-input input {\r\n\ttransition: box-shadow 0.2s !important;\r\n}\r\n\r\n#contact textarea:focus,\r\n#contact input:focus,\r\n.widget .search-blog-input input:focus {\r\n\ttransition: box-shadow 0.2s !important;\r\n\tbox-shadow: 0px 0px 10px 0px rgba(0,0,0,0.07);\r\n}\r\n\r\n/* Blog Widget Tabs */\r\n.widget-tabs { \r\n\tpadding: 0;\r\n\tlist-style: none;\r\n}\r\n\r\n.widget-tabs li {\r\n\tpadding: 0 0 27px 0;\r\n\tmargin: 24px 0 0 0;\r\n\tborder-bottom: 1px solid #e0e0e0;\r\n}\r\n\r\n.widget .tab-content { padding-bottom: 0; }\r\n\r\n.widget-thumb a img {\r\n\tdisplay: block;\r\n\twidth: 95px;\r\n\theight: 95px;