ansform 0.3s;\r\n\ttransition: transform 0.3s, color 0.3s 0s;\r\n\ttransition: transform 0.3s, color 0.3s 0s, -webkit-transform 0.3s;\r\n\tcolor: #333;\r\n\tdisplay: inline-block;\r\n}\r\n\r\n#posts-nav li a:hover { color: #66676b; }\r\n\r\n#posts-nav li.next-post {\r\n\tright: 0;\r\n\ttext-align: right;\r\n}\r\n\r\n#posts-nav li.prev-post {\r\n\tleft: 0;\r\n}\r\n\r\n#posts-nav li.next-post a { padding-right: 50px; }\r\n#posts-nav li.prev-post a { padding-left: 50px; }\r\n\r\n#posts-nav li.prev-post a:before,\r\n#posts-nav li.next-post a:before {\r\n\tfont-family: \"simple-line-icons\";\r\n\tfont-size: 20px;\r\n\tposition: absolute;\r\n\tcolor: inherit;\r\n\ttop: 50%;\r\n\t-webkit-transform: translate3d(0,-50%,0);\r\n\t        transform: translate3d(0,-50%,0);\r\n\ttransition: -webkit-transform 0.3s;\r\n\ttransition: transform 0.3s;\r\n\ttransition: transform 0.3s, -webkit-transform 0.3s;\r\n}\r\n\r\n#posts-nav li.next-post a:before {\r\n\tright: 0;\r\n\tcontent: \"\\E606\";\r\n}\r\n\r\n#posts-nav li.prev-post a:before {\r\n\tleft: 0;\r\n\tcontent: \"\\E605\"\r\n}\r\n\r\n#posts-nav li.next-post a:hover:before { -webkit-transform: translate3d(5px,-50%,0); transform: translate3d(5px,-50%,0); }\r\n#posts-nav li.prev-post a:hover:before { -webkit-transform: translate3d(-5px,-50%,0); transform: translate3d(-5px,-50%,0); }\r\n\r\n\r\n/* ---------------------------------- */\r\n/* Pricing Tables\r\n------------------------------------- */\r\n.pricing-container {\r\n\tdisplay: -webkit-box;\r\n\tdisplay: -ms-flexbox;\r\n\tdisplay: flex;\r\n\t-ms-flex-wrap: wrap;\r\n\t    flex-wrap: wrap;\r\n\t-webkit-box-align: start;\r\n\t    -ms-flex-align: start;\r\n\t        align-items: flex-start;\r\n\tposition: relative;\r\n}\r\n\r\n.plan {\r\n\t-webkit-box-flex: 1;\r\n\t    -ms-flex: 1;\r\n\t        flex: 1;\r\n\tbox-shadow: 0px 0px 12px 0px rgba(0,0,0,0.1); \r\n\ttext-align: center;\r\n\tposition: relative;\r\n\tz-index: 9;\r\n\toverflow: hidden;\r\n}\r\n\r\n.plan-price {\r\n\twidth