te;\r\n\ttop:0;\r\n\twidth: 50%;\r\n\tdisplay: block;\r\n\theight: 100%;\r\n\toverflow: hidden;\r\n}\r\n\r\n\r\n/* Like Icon */\r\nspan.like-icon {\r\n\tcolor: #fff;\r\n\tfont-size: 19px;\r\n\tposition: absolute;\r\n\tz-index: 101;\r\n\tright: 30px;\r\n\tbottom: 30px;\r\n\tcursor: normal;\r\n\tbackground-color: rgba(24,24,24,0.4);\r\n\tdisplay: block;\r\n\theight: 44px;\r\n\twidth: 44px;\r\n\tborder-radius: 50%;\r\n\ttransition: all 0.4s;\r\n}\r\n\r\n.like-icon:before,\r\n.like-icon:after {\r\n\ttransition: opacity 0.3s, -webkit-transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n\ttransition: opacity 0.3s, transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n\ttransition: opacity 0.3s, transform 0.4s cubic-bezier(.0,-.41,.19,2.5), -webkit-transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n\tfont-family: \"FontAwesome\";\r\n\tposition: absolute;\r\n\tright: 0;\r\n\tleft: 0;\r\n\ttext-align: center;\r\n\ttop: 9px;\r\n}\r\n\r\n.like-icon:before {\r\n\tcontent: \"\\F08A\";\r\n\t-webkit-transform: scale(0.95);\r\n\t        transform: scale(0.95);\r\n}\r\n\r\n.like-icon:after {\r\n\tcontent: \"\\F004\";\r\n\topacity: 0;\r\n\t-webkit-transform: scale(0.5);\r\n\t        transform: scale(0.5);\r\n}\r\n\r\n.like-icon.liked:after {\r\n\topacity: 1;\r\n\t-webkit-transform: scale(0.95);\r\n\t        transform: scale(0.95);\r\n}\r\n\r\n.like-icon:hover,\r\n.like-icon.liked {\r\n\tbackground-color: #f3103c;\r\n}\r\n\r\n.like-icon.liked:before{\r\n\topacity: 0;\r\n}\r\n\r\n/* Badges */\r\n.listing-item { overflow: hidden; }\r\n\r\n.listing-badge {\r\n\tbackground-color: #333;\r\n\tfloat: left;\r\n\tposition: absolute;\r\n\t-webkit-transform: rotate(45deg);\r\n\t        transform: rotate(45deg);\r\n\tright: -64px;\r\n\ttop: 22px;\r\n\ttext-align: center;\r\n\twidth: 200px;\r\n\tfont-size: 12.5px;\r\n\tmargin: 0;\r\n\tz-index: 999;\r\n\tcolor: #fff;\r\n\tfont-weight: 500;\r\n\tline-height: 28px;\r\n}\r\n\r\n\r\n.listing-badge.now-open {\r\n\tbackground-color: #54ba1d;\r\n}\r\n\r\n.listing-ba