ax #titlebar #breadcrumbs ul li,\r\n.parallax #titlebar #breadcrumbs ul li a,\r\n.parallax #titlebar #breadcrumbs ul li::before {color: #fff}\r\n\r\n.parallax #titlebar span a,\r\n.parallax #titlebar span { color: rgba(255,255,255,0.7); }\r\n\r\n\r\n\r\n/* ------------------------------------------------------------------- */\r\n/*  04. Dashboard\r\n---------------------------------------------------------------------- */\r\n#dashboard {\r\n\tbackground-color: #f7f7f7;\r\n\tmin-height: 100vh;\r\n\tdisplay: -webkit-box;\r\n\tdisplay: -ms-flexbox;\r\n\tdisplay: flex;\r\n\t-ms-flex-wrap: wrap;\r\n\t    flex-wrap: wrap;\r\n\tpadding-top: 80px;\r\n}\r\n\r\n/* Flexbox Adjustments */\r\n.dashboard-content,\r\n.dashboard-nav { -webkit-box-flex: 1; -ms-flex: 1; flex: 1; }\r\n\r\n.dashboard-content {\r\n\tpadding: 40px 45px;\r\n\tpadding-bottom: 0;\r\n\tposition: relative;\r\n\tz-index: 10;\r\n\theight: 100%;\r\n\tmargin-left: 260px;\r\n}\r\n\r\n.dashboard-nav,\r\n.dashboard #logo {\r\n\tmin-width: 260px;\r\n\tmax-width: 260px;\r\n}\r\n\r\n.dashboard-nav {\r\n\tposition: fixed;\r\n\tmin-height: 100vh;\r\n}\r\n\r\nheader.fullwidth.dashboard .container { padding-left: 285px; }\r\n\r\n/* Dashboard Logo */\r\n.dashboard #logo {\r\n\tdisplay: block;\r\n\tbackground-color: #333;\r\n\tposition: absolute;\r\n\theight: 100%;\r\n\tleft: 0;\r\n\ttop: 0;\r\n\tmargin: 0;\r\n}\r\n\r\n.dashboard #logo a {\r\n\tmargin-left: 33px;\r\n\tdisplay: inline-block;\r\n\theight: 100%;\r\n}\r\n\r\n.dashboard #logo a img {\r\n\ttop: 50%;\r\n\tposition: relative;\r\n\t-webkit-transform: translate3d(0,-50%,0);\r\n\t        transform: translate3d(0,-50%,0);\r\n}\r\n\r\n\r\n/* Dashboard Footer */\r\n#dashboard .copyrights {\r\n\tpadding: 40px 0;\r\n\tmargin: 0;\r\n\tdisplay: block;\r\n\ttext-align: left;\r\n\tborder: none;\r\n}\r\n\r\n.dashboard #logo a { display: none; }\r\n.dashboard #logo a.dashboard-logo { display: inline-block;}\r\n\r\n/* ---------------------------------- */\r\n/* Dashboard Navigation\r\n------------------------------------- */\r\n.dashboard-nav {\r\n\tbackground-color: #2a2a2a;\r\n\tdisplay: block;\r\n\tbox-shadow: 0px 0px 12px 0px rgba(0,0,0,0.12);\r\n\tz-index: 11;\r\n\tpadding: 10px 0 25px 0;\r\n}\r\n\r\na.dashboard-responsive-nav-trigger { display: none; }\r\n\r\n.dashboard-nav ul {\r\n\tlist-style: none;\r\n\tpadding: 0;\r\n\tmargin: 15px 0 0 0;\r\n}\r\n\r\n.dashboard-nav ul:before {\r\n\tcontent: attr(data-submenu-title);\r\n\tpadding: 0 30px;\r\n\tmargin-bottom: 5px;\r\n\tdisplay: block;\r\n\tcolor: #999;\r\n\ttext-transform: uppercase;\r\n\tfont-weight: 300;\r\n\tfont-size: 12px;\r\n\tletter-spacing: 1px;\r\n}\r\n\r\n/* 1st Level */\r\n.dashboard-nav ul li {\r\n\tcolor: #eee;\r\n\tdisplay: block;\r\n\tborder-left: 3px solid transparent;\r\n\ttransition: 0.3s;\r\n\tline-height: 24px;\r\n\tfont-size: 15px;\r\n}\r\n\r\n.dashboard-nav ul li a {\r\n\tcolor: #eee;\r\n\tdisplay: block;\r\n\tpadding: 11px 27px;\r\n\ttransition: 0.3s;\r\n\tcursor: pointer;\r\n}\r\n\r\n.dashboard-nav ul li a i {\r\n\tpadding-right: 8px;\r\n\twidth: 20px;\r\n}\r\n\r\n.dashboard-nav ul li a:hover { color: #fff; } \r\n\r\n\r\n/* Nav Tag */\r\n.dashboard-nav ul li span.nav-tag {\r\n\tbackground-color: rgba(255,255,255,0.2);\r\n\tfont-family: \"Open Sans\";\r\n\tfont-weight: 600;\r\n\tdisplay: inline-block;\r\n\tfont-size: 11px;\r\n\tline-height: 20px;\r\n\tcolor: #fff;\r\n\tpadding: 0;\r\n\tpadding: 0 7px;\r\n\tbox-sizing: border-box;\r\n\ttext-align: center;\r\n\tmin-width: 20px;\r\n\theight: 20px;\r\n\tletter-spacing: -0.5px;\r\n\ttext-align: center;\r\n\tborder-radius: 50px;\r\n\tmargin-left: 4px;\r\n}\r\n\r\n.dashboard-nav ul li span.nav-tag.messages {top: -2px; position: relative; }\r\n.dashboard-nav ul li span.nav-tag.messages,\r\n.dashboard-nav ul li span.nav-tag.green { background-color: #64bc36; }\r\n.dashboard-nav ul li span.nav-tag.yellow { background-color: #b9be1c; }\r\n.dashboard-nav ul li span.nav-tag.red { background-color: #ee3535; }\r\n\r\n\r\n/* Dropdown Styles */\r\n.dashboard-nav ul li ul {\r\n\tpadding: 0;\r\n\tmargin: 0;\r\n\tvisibility: hidden;\r\n\tmax-height: 0;\r\n\topacity: 0;\r\n\tline-height: 0;\r\n\ttransition: 0.2s;\r\n}\r\n\r\n.dashboard-nav ul li.active ul {\r\n\tvisibility: visible;\r\n\tpadding: 0 0 15px 0;\r\n\tline-height: 24px;\r\n\topacity: 1;\r\n\tmax-height: 300px;\r\n}\r\n\r\n.dashboard-nav ul li ul li {\r\n\tcolor: #eee;\r\n\tbackground-color: transparent;\r\n\tborder: none;\r\n\tpadding-left: 54px;\r\n\tposition: relative;\r\n\tmargin: 0;\r\n}\r\n\r\n.dashboard-nav ul li ul li a { padding: 7px 0; }\r\n\r\n.dashboard-nav ul li ul li:hover {\r\n\tcolor: #fff;\r\n\tborder: none;\r\n\tbackground-color: transparent;\r\n}\r\n\r\n/* Indicator Arrow */\r\n.dashboard-nav ul li a:after {\r\n\tfont-family: \"FontAwesome\";\r\n\tcontent: '\\F107';\r\n\tright: 27px;\r\n\tfont-size: 16px;\r\n\tposition: absolute;\r\n\ttransition: 0.2s;\r\n\tcolor: rgba(255,255,255,0.5);\r\n}\r\n\r\n\r\n.dashboard-nav ul li.active a:after { -webkit-transform: rotate(180deg); transform: rotate(180deg); }\r\n \r\n.dashboard-nav ul li ul:before,\r\n.dashboard-nav ul li a:only-child:after { content: ''; display: none; } \r\n\r\n/* Hover State */\r\n.dashboard-nav ul li ul li:hover a { padding-left: 4px; }\r\n\r\n.dashboard-nav ul li.active,\r\n.dashboard-nav ul li:hover {\r\n\tcolor: #fff;\r\n\tborder-color: #66676b;\r\n\tbackground-color: rgba(255,255,255,0.05);\r\n}\r\n\r\n\r\n/* ---------------------------------- */\r\n/* Dashboard Titlebar\r\n------------------------------------- */\r\n#dashboard #titlebar {\r\n\tpadding: 0;\r\n\tmargin: 0 0 40px 0;\r\n\tdisplay: block;\r\n\tbackground-color: transparent;\r\n\tbackground: transparent;\r\n}\r\n\r\n#dashboard #titlebar h2 {\r\n\tfont-size: 30px;\r\n\tline-height: 38px;\r\n}\r\n\r\n\r\n/* ---------------------------------- */\r\n/* Dashboard Stats Boxes\r\n------------------------------------- */\r\n.dashboard-stat {\r\n\tdisplay: inline-block;\r\n\tpadding: 0;\r\n\theight: 160px;\r\n\tbackground-color: #444;\r\n\tcolor: #fff;\r\n\tborder-radius: 4px;\r\n\twidth: 100%;\r\n\tposition: relative;\r\n\tmargin-bottom: 20px;\r\n\toverflow: hidden;\r\n\ttransition: 0.3s;\r\n\tcursor: default;\r\n}\r\n\r\n.dashboard-stat:hover { -webkit-transform: translateY(-4px); transform: translateY(-4px); }\r\n\r\n.dashboard-stat-content {\r\n\tposition: absolute;\r\n\tleft: 32px;\r\n\ttop: 50%;\r\n\twidth: 45%;\r\n\t-webkit-transform: translateY(-50%);\r\n\t        transform: translateY(-50%);\r\n}\r\n\r\n.dashboard-stat-content h4 {\r\n\tfont-size: 42px;\r\n\tfont-weight: 600;\r\n\tpadding: 0;\r\n\tmargin: 0;\r\n\tcolor: #fff;\r\n\tfont-family: \"Open Sans\";\r\n\tletter-spacing: -1px;\r\n}\r\n\r\n.dashboard-stat-content span {\r\n\tfont-size: 18px;\r\n\tmargin-top: 4px;\r\n\tline-height: 24px;\r\n\tfont-weight: 300;\r\n\tdisplay: inline-block;\r\n}\r\n\r\n.dashboard-stat-icon {\r\n\tposition: absolute;\r\n\tright: 32px;\r\n\ttop: 50%;\r\n\t-webkit-transform: translateY(-40%);\r\n\t        transform: translateY(-40%);\r\n\tfont-size: 80px;\r\n\topacity: 0.3;\r\n}\r\n\r\n\r\n/* Colors */\r\n.dashboard-stat.color-1 {\r\n\tbackground: linear-gradient(to left, rgba(255,255,255,0) 25%, rgba(255,255,255,0.2));\r\n\tbackground-color: #64bc36;\r\n}\r\n\r\n.dashboard-stat.color-2 {\r\n\tbackground: linear-gradient(to left, rgba(255,255,255,0) 25%, rgba(255,255,255,0.1));\r\n\tbackground-color: #363841;\r\n}\r\n\r\n.dashboard-stat.color-3 {\r\n\tbackground: linear-gradient(to left, rgba(255,255,255,0) 25%, rgba(255,255,255,0.3));\r\n\tbackground-color: #ffae00; \r\n}\r\n\r\n.dashboard-stat.color-4 {\r\n\tbackground: linear-gradient(to left, rgba(255,255,255,0) 25%, rgba(255,255,255,0.1));\r\n\tbackground-color: #f3103c;\r\n}\r\n\r\n\r\n/* ---------------------------------- */\r\n/* Dashboard Pagination\r\n------------------------------------- */\r\n#dashboard .pagination ul li a, \r\n#dashboard .pagination-next-prev ul li a {\r\n    background-color: tran